radius;\r\n    border: 0;\r\n    text-align: left;\r\n    font-family: inherit;\r\n    display: inline-block;\r\n    vertical-align: middle;\r\n    text-decoration: none;\r\n    -moz-appearance: none;\r\n    cursor: pointer;\r\n    width: 100%;\r\n    margin: .5rem 0;\r\n\r\n    &:hover {\r\n        background-color: $white;\r\n        box-shadow: rgba(0, 0, 0, .1) 0 0 0 1px, rgba(0, 0, 0, .1) 0 4px 16px;\r\n    }\r\n\r\n    > div {\r\n        align-items: center;\r\n        display: flex;\r\n    }\r\n\r\n    i {\r\n        box-sizing: content-box;\r\n        color: theme-color(\"primary\");\r\n        vertical-align: middle;\r\n        font-size: 1.5rem;\r\n    }\r\n\r\n    span {\r\n        display: inline-block;\r\n        font-size: 0.875rem;\r\n        line-height: 1.5;\r\n        margin-left: 16px;\r\n        overflow: hidden;\r\n        white-space: nowrap;\r\n        text-overflow: ellipsis;\r\n        vertical-align: middle;\r\n    }\r\n}\r\n","//\r\n// Brand buttons\r\n//\r\n\r\n\r\n// Color variations\r\n\r\n@each $color, $value in $brand-colors {\r\n    .btn-#{$color} {\r\n        @include button-variant($value, $value);\r\n    }\r\n}\r\n","//\r\n// Card\r\n//\r\n\r\n\r\n.card-translucent {\r\n    background-color: rgba(18, 91, 152, 0.08);\r\n}\r\n","//\r\n// Profile card\r\n//\r\n\r\n.card-profile-image {\r\n    position: relative;\r\n\r\n    img {\r\n        max-width: 80px;\r\n        border-radius: $border-radius;\r\n        @extend .shadow;\r\n        transform: translate(-50%,-30%);\r\n        position: absolute;\r\n        left: 50%;\r\n        transition: $transition-base;\r\n\r\n        &:hover {\r\n            transform: translate(-50%, -33%);\r\n        }\r\n    }\r\n}\r\n\r\n.card-profile-stats {\r\n    padding: 1rem 0;\r\n\r\n    > div {\r\n        text-align: center;\r\n        margin-right: 1rem;\r\n        padding: .875rem;\r\n\r\n        &:last-child {\r\n            margin-right: 0;\r\n        }\r\n\r\n        .heading {\r\n            font-size: 1.1rem;\r\n            font-weight: bold;\r\n            display: block;\r\n        }\r\n        .description {\r\n            font-size: .875rem;\r\n            color: $gray-500;\r\n        }\r\n    }\r\n}\r\n\r\n.card-profile-actions {\r\n    padding: .875rem;\r\n}\r\n","//\r\n// Card with blockquote\r\n//\r\n\r\n.card-blockquote {\r\n    padding: 2rem;\r\n    position: relative;\r\n\r\n    .svg-bg {\r\n        display: block;\r\n        width: 100%;\r\n        height: 95px;\r\n        position: absolute;\r\n        top: -94px;\r\n        left: 0;\r\n    }\r\n}\r\n","//\r\n// Card with hover animations\r\n//\r\n\r\n.card-lift--hover {\r\n    &:hover {\r\n        transform: translateY(-10px);\r\n        @include transition($transition-base);\r\n    }\r\n}\r\n","//\r\n// Card stats\r\n//\r\n\r\n.card-stats {\r\n    .card-body {\r\n        padding: 1rem 1.5rem;\r\n    }\r\n\r\n    .card-status-bullet {\r\n        position: absolute;\r\n        top: 0;\r\n        right: 0;\r\n        transform: translate(50%, -50%);\r\n    }\r\n}\r\n","//\r\n// Chart\r\n//\r\n\r\n.chart {\r\n    position: relative;\r\n    height: $chart-height;\r\n}\r\n\r\n\r\n// Size variations\r\n\r\n.chart-sm {\r\n    height: $chart-height-sm;\r\n}\r\n\r\n\r\n// Legend\r\n\r\n.chart-legend {\r\n    display: flex;\r\n    justify-content: center;\r\n    margin-top: $chart-legend-margin-top;\r\n    font-size: $chart-legend-font-size;\r\n    text-align: center;\r\n    color: $chart-legend-color;\r\n}\r\n\r\n.chart-legend-item {\r\n    display: inline-flex;\r\n    align-items: center;\r\n\r\n    + .chart-legend-item {\r\n        margin-left: 1rem;\r\n    }\r\n}\r\n\r\n.chart-legend-indicator {\r\n    display: inline-block;\r\n    width: 0.5rem;\r\n    height: 0.5rem;\r\n    margin-right: 0.375rem;\r\n    border-radius: 50%;\r\n}\r\n\r\n\r\n// Tooltip\r\n\r\n#chart-tooltip {\r\n    z-index: 0;\r\n\r\n    .arrow {\r\n        top: 100%;\r\n        left: 50%;\r\n        transform: translateX(-50%) translateX(-.5rem);\r\n    }\r\n}\r\n\r\n\r\n// Chart info overlay\r\n\r\n.chart-info-overlay {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 5%;\r\n  max-width: 350px;\r\n  padding: 20px;\r\n  z-index: 1;\r\n}\r\n","//\r\n// Close\r\n//\r\n\r\n.close {\r\n    @if $enable-transitions {\r\n        transition: $transition-base;\r\n    }\r\n    \r\n    &>span:not(.sr-only) {\r\n        background-color: $close-bg;\r\n        color: $close-color;\r\n        line-height: 17px;\r\n        height: 1.25rem;\r\n        width: 1.25rem;\r\n        border-radius: 50%;\r\n        font-size: 1.25rem;\r\n        display: block;\r\n        @if $enable-transitions {\r\n            transition: $transition-base;\r\n        }\r\n    }\r\n\r\n    &:hover,\r\n    &:focus {\r\n        background-color: $close-hover-bg;\r\n        color: $close-hover-color;\r\n        outline: none;\r\n\r\n        span:not(.sr-only) {\r\n            background-color: $close-hover-bg;\r\n        }\r\n    }\r\n}\r\n",".main-content {\r\n    position: relative;\r\n\r\n    // Navbar\r\n    .navbar-top {\r\n        position: absolute;\r\n        left: 0;\r\n        top: 0;\r\n        width: 100%;\r\n        z-index: 1;\r\n        background-color: transparent;\r\n        padding-left: 0 !important;\r\n        padding-right: 0 !important;\r\n    }\r\n\r\n    // Container\r\n    .container-fluid {\r\n        @include media-breakpoint-up(md) {\r\n            padding-left: ($main-content-padding-x + $grid-gutter-width / 2) !important;\r\n            padding-right: ($main-content-padding-x + $grid-gutter-width / 2) !important;\r\n        }\r\n    }\r\n}\r\n\r\n\r\n// Offsets the main content depending on the sidebar positioning\r\n\r\n.navbar-vertical.navbar-expand {\r\n\r\n    @each $breakpoint,\r\n    $dimension in $grid-breakpoints {\r\n\r\n        &-#{$breakpoint} {\r\n\r\n            @include media-breakpoint-up(#{$breakpoint}) {\r\n\r\n                // Left\r\n                &.fixed-left + .main-content {\r\n                    margin-left: $navbar-vertical-width;\r\n                } // Right\r\n                &.fixed-right + .main-content {\r\n                    margin-right: $navbar-vertical-width;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n","//\r\n// Custom checkbox\r\n//\r\n\r\n.custom-checkbox {\r\n    .custom-control-input ~ .custom-control-label {\r\n        cursor: pointer;\r\n        font-size: $font-size-sm;\r\n    }\r\n\r\n    .custom-control-input {\r\n        &:checked {\r\n            ~ .custom-control-label {\r\n                &::before {\r\n                    border-color: $custom-control-indicator-checked-border-color;\r\n                }\r\n                &::after {\r\n                    background-image: $custom-checkbox-indicator-icon-checked;\r\n                }\r\n            }\r\n        }\r\n\r\n        &:disabled {\r\n            ~ .custom-control-label {\r\n                &::before {\r\n                    border-color: $custom-control-indicator-disabled-bg;\r\n                }\r\n            }\r\n\r\n            &:checked {\r\n                &::before {\r\n                    border-color: $custom-control-indicator-checked-disabled-bg;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n","//\r\n// Custom control\r\n// additional styles for custom checkboxes, radios and other\r\n//\r\n\r\n.custom-control-label {\r\n    // Background-color and (when enabled) gradient\r\n    &::before {\r\n        border: $custom-control-indicator-border-width solid $custom-control-indicator-border-color;\r\n        @if $enable-transitions {\r\n            transition: $input-transition;\r\n        }\r\n    }\r\n\r\n    span {\r\n        position: relative;\r\n        top: 2px;\r\n    }\r\n}\r\n\r\n.custom-control-label {\r\n    margin-bottom: 0;\r\n}\r\n\r\n\r\n// Alternative style\r\n\r\n.custom-control-alternative {\r\n    .custom-control-label {\r\n        // Background-color and (when enabled) gradient\r\n        &::before {\r\n            border: 0;\r\n            box-shadow: $input-alternative-box-shadow;\r\n        }\r\n    }\r\n\r\n    .custom-control-input {\r\n        &:checked {\r\n            ~ .custom-control-label {\r\n                &::before {\r\n                    box-shadow: $input-focus-alternative-box-shadow;\r\n                }\r\n            }\r\n        }\r\n\r\n        &:active~.custom-control-label::before,\r\n        &:focus~.custom-control-label::before {\r\n            box-shadow: $input-alternative-box-shadow;\r\n        }\r\n    }\r\n}\r\n","//\r\n// Custom checkbox\r\n//\r\n\r\n.custom-checkbox {\r\n    .custom-control-input ~ .custom-control-label {\r\n        cursor: pointer;\r\n        font-size: $font-size-sm;\r\n    }\r\n\r\n    .custom-control-input {\r\n        &:checked {\r\n            ~ .custom-control-label {\r\n                &::before {\r\n                    border-color: $custom-control-indicator-checked-border-color;\r\n                }\r\n                &::after {\r\n                    background-image: $custom-checkbox-indicator-icon-checked;\r\n                }\r\n            }\r\n        }\r\n\r\n        &:disabled {\r\n            ~ .custom-control-label {\r\n                &::before {\r\n                    border-color: $custom-control-indicator-disabled-bg;\r\n                }\r\n            }\r\n\r\n            &:checked {\r\n                &::before {\r\n                    border-color: $custom-control-indicator-checked-disabled-bg;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n","//\r\n// Custom radio\r\n//\r\n\r\n.custom-radio {\r\n    .custom-control-input ~ .custom-control-label {\r\n        cursor: pointer;\r\n        font-size: $font-size-sm;\r\n    }\r\n\r\n    .custom-control-input {\r\n        &:checked {\r\n            ~ .custom-control-label {\r\n                &::before {\r\n                    border-color: $custom-control-indicator-checked-border-color;\r\n                }\r\n                &::after {\r\n                    background-image: $custom-radio-indicator-icon-checked;\r\n                }\r\n            }\r\n        }\r\n\r\n        &:disabled {\r\n            ~ .custom-control-label {\r\n                &::before {\r\n                    border-color: $custom-control-indicator-disabled-bg;\r\n                }\r\n            }\r\n\r\n            &:checked {\r\n                &::before {\r\n                    border-color: $custom-control-indicator-checked-disabled-bg;\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n","//\r\n// Custom toggle\r\n//\r\n\r\n.custom-toggle {\r\n    position: relative;\r\n    display: inline-block;\r\n    width: $custom-toggle-width;\r\n    height: 1.5rem;\r\n\r\n    input {\r\n        display: none;\r\n\r\n        &:checked {\r\n             + .custom-toggle-slider {\r\n                border: $custom-control-indicator-border-width solid $custom-control-indicator-checked-border-color;\r\n\r\n                &:before {\r\n                    background: $custom-toggle-checked-bg;\r\n                    transform: translateX(1.625rem);\r\n                }\r\n            }\r\n        }\r\n\r\n        &:disabled {\r\n            + .custom-toggle-slider {\r\n                border: $custom-control-indicator-border-width solid $custom-control-indicator-disabled-bg;\r\n            }\r\n\r\n            &:checked {\r\n                + .custom-toggle-slider {\r\n                    border: $custom-control-indicator-border-width solid $custom-control-indicator-disabled-bg;\r\n\r\n                    &:before {\r\n                        background-color: lighten($custom-control-indicator-checked-bg, 10%);\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.custom-toggle-slider {\r\n    position: absolute;\r\n    cursor: pointer;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    border: $custom-control-indicator-border-width solid $input-border-color;\r\n    border-radius: 34px !important;\r\n    background-color: transparent;\r\n\r\n\r\n    &:before {\r\n        position: absolute;\r\n        content: \"\";\r\n        height: 18px;\r\n        width: 18px;\r\n        left: 2px;\r\n        bottom: 2px;\r\n        border-radius: 50% !important;\r\n        background-color: $custom-toggle-slider-bg;\r\n        transition: $input-transition;\r\n    }\r\n}\r\n","//\r\n// Dropdown\r\n//\r\n\r\n// General styles\r\n\r\n.dropdown,\r\n.dropup,\r\n.dropright,\r\n.dropleft {\r\n    display: inline-block;\r\n}\r\n\r\n.dropdown-menu {\r\n    min-width: 12rem;\r\n\r\n    .dropdown-item {\r\n        padding: .5rem 1rem;\r\n        font-size: $font-size-sm;\r\n        > i,\r\n        > svg {\r\n            margin-right: 1rem;\r\n            font-size: 1rem;\r\n            vertical-align: -17%;\r\n        }\r\n    }\r\n}\r\n\r\n.dropdown-header {\r\n    padding-left: 1rem;\r\n    padding-right: 1rem;\r\n    color: $gray-100;\r\n    font-size: .625rem;\r\n    text-transform: uppercase;\r\n    font-weight: 700;\r\n}\r\n\r\n\r\n// Media components inside dropdown link\r\n\r\n.dropdown-menu {\r\n    a.media {\r\n\r\n        > div {\r\n            &:first-child {\r\n                line-height: 1;\r\n            }\r\n        }\r\n\r\n        p {\r\n            color: $gray-600;\r\n        }\r\n\r\n        &:hover {\r\n            .heading,\r\n            p {\r\n                color: theme-color(\"default\") !important;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n\r\n// Size variations\r\n\r\n.dropdown-menu-sm {\r\n    min-width: 100px;\r\n    border: $border-radius-lg;\r\n}\r\n\r\n.dropdown-menu-lg {\r\n    min-width: 260px;\r\n    border-radius: $border-radius-lg;\r\n}\r\n\r\n.dropdown-menu-xl {\r\n    min-width: 450px;\r\n    border-radius: $border-radius-lg;\r\n}\r\n","//\r\n// Footer\r\n//\r\n\r\n\r\n// General styles\r\n\r\n.footer {\r\n    background: $footer-bg;\r\n    padding: $footer-padding-y $footer-padding-x;\r\n\r\n    .col-footer {\r\n        .heading {\r\n            color: $footer-heading-color;\r\n            letter-spacing: 0;\r\n            font-size: $footer-heading-font-size;\r\n            text-transform: uppercase;\r\n            font-weight: $font-weight-bold;\r\n            margin-bottom: 1rem;\r\n        }\r\n    }\r\n\r\n    .nav .nav-item .nav-link,\r\n    .footer-link {\r\n        color: $footer-link-color !important;\r\n\r\n        &:hover {\r\n            color: $footer-link-hover-color !important;\r\n        }\r\n    }\r\n\r\n    .list-unstyled li a {\r\n        display: inline-block;\r\n        padding: .125rem 0;\r\n        color: $footer-link-color;\r\n        font-size: $footer-link-font-size;\r\n\r\n        &:hover {\r\n            color: $footer-link-hover-color;\r\n        }\r\n    }\r\n\r\n    .copyright {\r\n        font-size: $font-size-sm;\r\n    }\r\n}\r\n\r\n\r\n// Dark footer\r\n\r\n.footer-dark {\r\n    .col-footer .heading {\r\n        color: $white;\r\n    }\r\n}\r\n\r\n\r\n// Footer nav used for copyright and some links, but not limited to this\r\n\r\n.nav-footer {\r\n    .nav-link {\r\n        font-size: $font-size-sm;\r\n    }\r\n\r\n    .nav-item:last-child {\r\n        .nav-link {\r\n            padding-right: 0;\r\n        }\r\n    }\r\n}\r\n\r\n\r\n// Footer with cards over\r\n\r\n.footer.has-cards {\r\n    overflow: hidden;\r\n    padding-top: 500px;\r\n    margin-top: -420px;\r\n    position: relative;\r\n    background: transparent;\r\n    pointer-events: none;\r\n\r\n    &:before {\r\n        content: \"\";\r\n        position: absolute;\r\n        left: 0;\r\n        right: 0;\r\n        top: 600px;\r\n        height: 2000px;\r\n        background: theme-color(\"secondary\");\r\n        transform: skew(0,-8deg);\r\n    }\r\n\r\n    .container {\r\n        pointer-events: auto;\r\n        position: relative;\r\n    }\r\n}\r\n","//\r\n// Forms\r\n//\r\n\r\n\r\n// Labels\r\n\r\n.form-control-label {\r\n    color: $gray-700;\r\n    font-size: $font-size-sm;\r\n    font-weight: $font-weight-bold;\r\n}\r\n\r\n\r\n// Text inputs\r\n\r\n.form-control {\r\n    font-size: $input-btn-font-size;\r\n\r\n    &:focus {\r\n        &::placeholder {\r\n            color: $input-focus-placeholder-color;\r\n        }\r\n    }\r\n}\r\n\r\n\r\n// Textarea\r\n\r\ntextarea[resize=\"none\"] {\r\n    resize: none!important;\r\n}\r\n\r\ntextarea[resize=\"both\"] {\r\n    resize: both!important;\r\n}\r\n\r\ntextarea[resize=\"vertical\"] {\r\n    resize: vertical!important;\r\n}\r\n\r\ntextarea[resize=\"horizontal\"] {\r\n    resize: horizontal!important;\r\n}\r\n\r\n\r\n// Form input variations\r\n\r\n// Muted input\r\n\r\n.form-control-muted {\r\n    background-color: $input-muted-bg;\r\n    border-color: $input-muted-bg;\r\n    box-shadow: none;\r\n\r\n    &:focus {\r\n        background-color: $input-focus-muted-bg;\r\n    }\r\n}\r\n\r\n\r\n// Alternative input\r\n\r\n.form-control-alternative {\r\n    box-shadow: $input-alternative-box-shadow;\r\n    border: 0;\r\n    transition: box-shadow .15s ease;\r\n\r\n    &:focus {\r\n        box-shadow: $input-focus-alternative-box-shadow;\r\n    }\r\n}\r\n\r\n// Size variations: Fixes to the bootstrap defaults\r\n\r\n.form-control-lg {\r\n    font-size: $font-size-base;\r\n}\r\n","//\r\n// Form validation\r\n//\r\n\r\n// Validation\r\n\r\n.has-success,\r\n.has-danger {\r\n    position: relative;\r\n\r\n    &:after, {\r\n        width: 19px;\r\n        height: 19px;\r\n        line-height: 19px;\r\n        text-align: center;\r\n        font-family: 'NucleoIcons';\r\n        display: inline-block;\r\n        position: absolute;\r\n        right: 15px;\r\n        top: 2px;\r\n        transform: translateY(50%);\r\n        border-radius: 50%;\r\n        font-size: 9px;\r\n        opacity: 1;\r\n    }\r\n}\r\n\r\n.has-success {\r\n    &:after {\r\n        content: \"\\ea26\";\r\n        color: daken($form-feedback-valid-color, 18%);\r\n        background-color: $form-feedback-valid-bg;\r\n    }\r\n    \r\n    .form-control {\r\n        background-color: $input-focus-bg;\r\n\r\n        &:focus {\r\n            border-color: $input-focus-border-color;\r\n        }\r\n\r\n\r\n        // Placeholder\r\n\r\n        &::placeholder {\r\n            color: $form-feedback-valid-color;\r\n        }\r\n    }\r\n}\r\n\r\n.has-danger {\r\n    &:after {\r\n        content: \"\\ea53\";\r\n        color: daken($form-feedback-invalid-color, 18%);\r\n        background-color: $form-feedback-invalid-bg;\r\n    }\r\n\r\n    .form-control {\r\n        background-color: $input-focus-bg;\r\n\r\n        &:focus {\r\n            border-color: $input-focus-border-color;\r\n        }\r\n\r\n        // Placeholder\r\n\r\n        &::placeholder {\r\n            color: $form-feedback-invalid-color;\r\n        }\r\n    }\r\n}\r\n","//\r\n// Input group\r\n//\r\n\r\n.input-group {\r\n\tbox-shadow: $input-box-shadow;\r\n\tborder-radius: $input-border-radius;\r\n\ttransition: $transition-base;\r\n\r\n\t.form-control {\r\n\t\tbox-shadow: none;\r\n\r\n\t\t&:not(:first-child) {\r\n\t\t    border-left: 0;\r\n\t\t    padding-left: 0;\r\n\t\t}\r\n\t\t&:not(:last-child) {\r\n\t\t\tborder-right: 0;\r\n\t\t\tpadding-right: 0;\r\n\t\t}\r\n\t\t&:focus {\r\n\t\t\tbox-shadow: none;\r\n\t\t}\r\n\t}\r\n}\r\n\r\n.input-group-text {\r\n    transition: $input-transition;\r\n}\r\n\r\n\r\n// Alternative input groups related to .form-control-alternative\r\n\r\n\r\n.input-group-alternative {\r\n    box-shadow: $input-alternative-box-shadow;\r\n    border: 0;\r\n    transition: box-shadow .15s ease;\r\n\r\n    .form-control,\r\n    .input-group-text {\r\n        border: 0;\r\n        box-shadow: none;\r\n    }\r\n}\r\n\r\n.focused {\r\n\t.input-group-alternative {\r\n        box-shadow: $input-focus-alternative-box-shadow !important;\r\n    }\r\n}\r\n\r\n\r\n// .focus class is applied dinamycally from theme.js\r\n\r\n.focused {\r\n\t.in