w-navbar-collapse .2s ease forwards;\r\n    }\r\n\r\n    .navbar-collapse.collapsing-out {\r\n        animation: hide-navbar-collapse .2s ease forwards;\r\n    }\r\n}\r\n\r\n@keyframes show-navbar-collapse {\r\n    0% {\r\n        opacity: 0;\r\n        transform: scale(.95);\r\n        transform-origin: 100% 0;\r\n    }\r\n\r\n    100% {\r\n        opacity: 1;\r\n        transform: scale(1);\r\n    }\r\n}\r\n\r\n@keyframes hide-navbar-collapse {\r\n    from {\r\n        opacity: 1;\r\n        transform: scale(1);\r\n        transform-origin: 100% 0;\r\n    }\r\n\r\n    to {\r\n        opacity: 0;\r\n        transform: scale(.95);\r\n    }\r\n}\r\n","//\r\n// Pagination\r\n//\r\n\r\n\r\n.page-item {\r\n    &.active .page-link {\r\n        box-shadow: $pagination-active-box-shadow;\r\n    }\r\n\r\n    .page-link,\r\n    span {\r\n        display: flex;\r\n        align-items: center;\r\n        justify-content: center;\r\n        padding: 0;\r\n        margin: 0 3px;\r\n        border-radius: 50% !important;\r\n        width: 36px;\r\n\t\theight: 36px;\r\n\t\tfont-size: $font-size-sm;\r\n    }\r\n}\r\n\r\n\r\n// Size variations\r\n\r\n.pagination-lg {\r\n    .page-item {\r\n        .page-link,\r\n        span {\r\n            width: 46px;\r\n            height: 46px;\r\n            line-height: 46px;\r\n        }\r\n    }\r\n}\r\n\r\n.pagination-sm {\r\n    .page-item {\r\n        .page-link,\r\n        span {\r\n            width: 30px;\r\n            height: 30px;\r\n            line-height: 30px;\r\n        }\r\n    }\r\n}\r\n","//\r\n// Popover\r\n//\r\n\r\n\r\n.popover {\r\n    border: 0;\r\n}\r\n\r\n.popover-header {\r\n\tfont-weight: $font-weight-bold;\r\n}\r\n\r\n\r\n// Alternative colors\r\n\r\n@each $color, $value in $theme-colors {\r\n    .popover-#{$color} {\r\n        @include popover-variant($value);\r\n    }\r\n}\r\n","@mixin popover-variant($background) {\r\n\r\n\tbackground-color: $background;\r\n\r\n\t.popover-header {\r\n\t\tbackground-color: $background;\r\n\t\tcolor: color-yiq($background);\r\n\t}\r\n\r\n\t.popover-body {\r\n\t\tcolor: color-yiq($background);\r\n\t}\r\n\t.popover-header{\r\n\t\tborder-color: rgba(color-yiq($background), .2);\r\n\t}\r\n\t&.bs-popover-top {\r\n\t\t.arrow::after {\r\n\t\t\tborder-top-color: $background;\r\n\t\t}\r\n\t}\r\n\t&.bs-popover-right {\r\n\t\t.arrow::after {\r\n\t\t\tborder-right-color: $background;\r\n\t\t}\r\n\t}\r\n\t&.bs-popover-bottom {\r\n\t\t.arrow::after {\r\n\t\t\tborder-bottom-color: $background;\r\n\t\t}\r\n\t}\r\n\t&.bs-popover-left {\r\n\t\t.arrow::after {\r\n\t\t\tborder-left-color: $background;\r\n\t\t}\r\n\t}\r\n}","//\r\n// Progress\r\n//\r\n\r\n\r\n// Progress container\r\n\r\n.progress-wrapper {\r\n    position: relative;\r\n    padding-top: 1.5rem;\r\n}\r\n\r\n\r\n// General styles\r\n\r\n.progress {\r\n    height: 8px;\r\n    margin-bottom: $spacer;\r\n    overflow: hidden;\r\n    border-radius: $border-radius-sm;\r\n    background-color: $progress-bg;\r\n    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);\r\n\r\n    .sr-only {\r\n        width: auto;\r\n        height: 20px;\r\n        margin: 0 0 0 30px;\r\n        left: 0;\r\n        clip: auto;\r\n        line-height: 20px;\r\n        font-size: 13px;\r\n    }\r\n}\r\n\r\n\r\n// Progress inner elements\r\n\r\n.progress-heading {\r\n    font-size: 14px;\r\n    font-weight: 500;\r\n    margin: 0 0 2px;\r\n    padding: 0;\r\n}\r\n\r\n.progress-bar {\r\n    box-shadow: none;\r\n    border-radius: 0;\r\n    height: auto;\r\n}\r\n\r\n.progress-info{\r\n    margin-bottom: .5rem;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n}\r\n\r\n.progress-label {\r\n    span {\r\n        display: inline-block;\r\n        color: $primary;\r\n        font-size: .625rem;\r\n        font-weight: 600;\r\n        text-transform: uppercase;\r\n        background: rgba($primary, .1);\r\n        padding: .25rem 1rem;\r\n        border-radius: 30px;\r\n    }\r\n}\r\n\r\n.progre